import React from 'react';
import { Accordion, Icon } from 'semantic-ui-react'
import { Link } from 'react-router-dom';
import '../App.css'

class Home extends React.Component {
  state = {
    activeIndex: ""
  }

  handleClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  render() {
    const { activeIndex } = this.state
    return(
    <div>
      <h1>Tervetuloa</h1>
      <p>
      Tll voit tarkastella eduskunnan kyttytymist suhteessa omiin nkemyksiisi.
        <br />
        <br />
      Sivustolla on vaalikone, jonka avulla voit katsoa,
      mik puolue on nestnyt eduskunnassa lhimmksi sinun nkemyksisi. Vaalikoneen kysymykset
      on otettu suoraan eduskunnan nestyksist, mink vuoksi ne voivat tuntua vaikeilta.
      Niss kysymyksiss on kuitenkin se etu, ett vastaukset kuvaavat eduskuntaryhmien
      todellista kyttytymist sen sijaan, ett tulos muodostuisi aiotun kyttytymisen perusteella.
      Ennen vaaleja annetut lupaukset eivt siis vaikuta tmn vaalikoneen tuloksiin.
        <br />
        <br />
      Lisksi vaalikone tarjoaa mahdollisuuden tarkastella eduskuntaryhmien nestykyttytymist
      eduskunnassa suhteessa
        <a target="_blank" rel="noopener noreferrer" href="https://yle.fi/uutiset/3-7869597"> ylen vaalikoneessa. </a>
      Vaalikoneen avulla voit siis arvioida mys sit,
      kuinka hyvin ennen vaaleja annetut lupaukset ovat pitneet.
      </p>
        <Link
          to="/kone"
        >
          <button className="button">Aloita</button>
        </Link>
        <Accordion style={{paddingTop: "1em", paddingBottom:"1em"}}>
          <Accordion.Title active={activeIndex === 0} index={0} onClick={this.handleClick}>
            <Icon name='dropdown' />
            <b>Miksi puoleen valinta on trkeit?</b>
          </Accordion.Title>
          <Accordion.Content active={activeIndex === 0} style={{marginLeft: "2em"}}>
            <p>
              Ryhmkuri
            </p>
          </Accordion.Content>

          <Accordion.Title active={activeIndex === 1} index={1} onClick={this.handleClick}>
            <Icon name='dropdown' />
            <b>Kuinka poliittinen jrjestelm toimii?</b>
          </Accordion.Title>
          <Accordion.Content active={activeIndex === 1} style={{marginLeft: "2em"}}>
            <p>
              Hallitus/oppositio
            </p>
          </Accordion.Content>
        </Accordion>
    </div>
    )
  }
}

export default Home
